*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

//dark
.dark{
  background-color: #121212;
  color: #fff;
}
.btn__dark{
  background-size: contain;
  background-image: url(../images/picto_theme.svg);
  background-repeat: no-repeat;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  padding: 0;
  width: 44px;
  height: 44px;
  margin-right: 10%;
  background-color: inherit;
  border: none;
  cursor: pointer;
}

//nav
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background-color: #121212 ;
  font-family: 'Roboto', sans-serif;
}
.logo{
  color: #cccccc;
  font-size: 20px;
}
.highlight{
  color: #ff5d5d;
  font-weight: 600;
}
.dark .highlight{
  color: #ff6d6d;
}
.logo a{
  text-decoration: none;
  color: #cccccc;
  font-size: inherit;
}
.nav-links{
  display: flex;
  justify-content: space-around;
  width: 40%;
}
.nav-links a:hover{
  border-bottom: 3px solid #ff6d6d ;
}
.nav-links a.active{
  border-bottom: 3px solid #ff6d6d ;
}
.nav-links li{
  list-style: none;
}
.nav-links a{
  color: #cccccc;
  text-decoration: none;
  letter-spacing: 3px;
  font-weight: bold;
  font-size: 16px;
}
.btn{
  cursor: pointer;
}
.burger{
  display: none;
  cursor: pointer;
}
.burger div{
  width: 25px;
  height: 2px;
  background-color: #cccccc;
  margin: 5px;
  transition: all 0.3s ease;
}
@media screen and (max-width:768px) {
  body{
    overflow-x: hidden;
  }
  .nav-links{
    position: absolute;
    right: 0px;
    height: 92vh;
    top: 8vh;
    background-color: #121212;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
    display: none;
    justify-content: initial;
  }
  .nav-links li{
    opacity: 0;
    margin-top: 10vh;
  }
  .burger{
    display: block;
  }
}
.nav-active{
  transform: translateX(0%);
  display: inherit;
}
@keyframes navLinkFade{
  from{
    opacity: 0;
    transform: translateX(50px);
  }
  to{
    opacity: 1;
    transform: translateX(0px);
  }
}
.toggle .line1{
  transform: rotate(-45deg) translate(-5px,6px);
}
.toggle .line2{
  opacity: 0;
}
.toggle .line3{
  transform: rotate(45deg) translate(-5px,-6px);
}


//footer
hr{
  display: none;
  height: 2px;
  background-color: #FF6D6D;
  border: none;
}
.dark hr{
  display: inherit;
}
.menu__el--footer a{
  text-decoration: none;
  color: #cccccc;
}
.section--footer{
  margin-bottom: 0;
  padding: 20px 0;
  background-color: #121212;
}
.container{
  width: 90%;
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 20px;
}
.menu__liste{
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.menu__el--footer{
  margin-right: 15px;
}
.section--footer .container{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.copyright{
  display: flex;
  text-align: center;
  justify-content: center;
  color: #cccccc;
}
.dwm{
  display: inline-block;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  background-image: url(../images/dwm.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.menu__el--footer a:hover{
  border-bottom: 3px solid #ff6d6d ;
}

